.App {
  text-align: center;
  font-family: "Courier New";
  background-color: white;
  font-size: 1rem;
}

.body {
  min-height: 100%;
  padding: 1rem 0 0;
}

.header {
  background-color: #2A2F33;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 4rem;
  color: white;
}

footer {
  background-color: #DAF7A6;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.sample-text-box {
  border: 3px solid black;
  width: 50%;
  padding: 4rem 1rem;
  margin: 5rem auto 0;
  font-size: 2rem;
  background-color: white;
}

.sample-text-container {
  position: relative;
}

.sample-text {
  font-weight: bold;
}

/* User Input */
.user-input {
  margin: 5.5rem auto 0rem;
}

.user-input textarea {
  border: 0;
  border-bottom: 2px solid black;
  width: 50%;
  padding: 0;
  font-size: 2.5rem;
  overflow: hidden;
  height: 4rem;
}

.user-input textarea:focus {
  outline: none;
}

/* Stats */
#stats-holder {
  display: none;
  width: 60%;
  margin: 0 auto;
}

#stats-holder button {
  margin: 5rem 2rem;
  padding: 1rem 0.5rem;
  background-color: #EF7C8E;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
}

/* Icons */
.FaKeyboard {
  height: 150px;
  width: 150px;
}

/* Try Again Animation */

.try-again-btn {
  display: none;
  background-color: green;
  color: white;
  font-weight: bold;
  padding-left: 2rem;
  padding-right: 2rem;
}

.try-again-btn:hover {
  background-color: #DAF7A6;
}

.try-again-btn span {
  display: inline-block;
  animation: wave-text 2s ease-in-out infinite;
  letter-spacing: 2px;
}

.try-again-btn .try-3 {
  margin: 0 8px;
}

@keyframes wave-text{
  00%{
    transform: translateY(0.2rem);
  }
  60%{
    transform: translateY(-0.2rem);
  }
  100%{
    transform: translateY(0.2rem);
  }
}

/* ///////////////////////////////////////////////////////////////////////////////////////////////////// */

 @media only screen and (min-width: 768px) and (max-width: 1424px) {

   .sample-text-box {
     margin: 2.5rem auto 0;
     padding: 5rem 0;
     width: 70%;
   }

   .user-input {
     margin: 3rem auto 0rem;
   }

   .user-input textarea {
     margin: 1rem auto 0rem;
     width: 65%;
   }

   #stats-holder {
     width: 85%;
   }

   #stats-holder button {
     margin: 5rem 1.25rem;
     padding: 1rem 0rem;
   }

   .try-again-btn {
     padding: 1rem 2rem;
   }
 }

/* ///////////////////////////////////////////////////////////////////////////////////////////////////// */

 @media only screen and (max-width: 767px) {

   .sample-text-box {
     margin: 2rem auto 0;
     width: 95%;
     padding: 4rem 0.25rem;
   }

   .user-input {
     margin: 4.5rem auto 0rem;
   }

   .user-input textarea {
     margin: 0rem auto 0rem;
     width: 90%;
   }

   #stats-holder {
     margin: 3rem auto;
     width: 95%;
   }

   #stats-holder button {
     margin: 0.6rem 0rem;
   }

   .try-again-btn {
     padding: 1rem 2rem;
   }
 }
